import React, {Component} from "react";
import {connect} from "react-redux";
import {OraSelect, OraInput, OraSimpleRadio} from "eshop/components/OraCommonComponents";
import {DatePicker} from "eshop/modules/core/components/ui/Datepicker";
import ErrorRow from "eshop/modules/core/components/hoc/ErrorRow";
import LabeledInput from "eshop/modules/core/components/hoc/LabeledInput";
import MulticartMnpTooltip from "./MulticartMnpTooltip";
import MulticartMnpBusinessAddressComponent from "./MulticartMnpBusinessAddressComponent";
import {registerNextStepCondition, requestRecalculateConsentsStrategy} from "../../actions/data";
import {Expander, Section, Toggle} from "eshop/modules/core/components/ui/Expander";
import {switchSameMnpData} from "../../actions/app";
import {isMnpDataFilled, getLegalForm, getMnpContactMethods} from "eshop/modules/checkout/selectors";
import OnlineUtils from 'eshop/utils/OnlineUtils'

const DateRangeBoundaries = {
    MIN: 'minDate',
    MAX: 'maxDate'
};

class MulticartMnpSectionComponent extends Component {
    constructor(props) {
        super(props);
    }

    componentDidUpdate(prevProps) {
        this.validateSelectedContactMethod();
    }

    validateSelectedContactMethod() {
        let contactMethods = this.getContactMethods();
        let selectedContactMethod = this.props.entry.contactMethod;
        if(contactMethods.find(contactMethod => contactMethod.value === selectedContactMethod)){
            return;
        }
        this.onValueChange({name: 'contactMethod', value: contactMethods[0].value});
    }

    wrapWithBundleNo(name) {
        return 'mnpData-' + name + '-' + this.props.entry.bundleNo;
    }

    onSimpleRadioValueChange(event) {
        let {name, value, checked} = event.target;
         this.props.changeCustomerMnpDataFormField({name, value, checked, entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
    }

    componentWillReceiveProps(props) {
        if(this.props.isB2B !== props.isB2B && props.legalForm==='JDG') {
            this.props.changeCustomerMnpDataFormField({name: 'agreementType',value: '2', entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
        } else if (this.props.isB2B && props.legalForm==='JDG' && this.props.legalForm!=='JDG') {
            this.props.changeCustomerMnpDataFormField({name: 'agreementType',value: '2', entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
        } else if (this.props.isB2B && props.legalForm!=null && props.legalForm!=='JDG' && (this.props.legalForm==='JDG' || this.props.legalForm==null)) {
            this.props.changeCustomerMnpDataFormField({name: 'agreementType',value: '3', entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
        }
    }


    onAgreementTypeChange(event) {
         this.props.changeCustomerMnpDataFormField({name: 'agreementType',value: event.target.value, entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
    }

    onHeadquartersAddressSameChange(event) {
        if (event.target.value == "true"){
            this.props.changeCustomerMnpDataFormField({name: 'isHeadquartersAddressSame',value: true, entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
        } else{
            this.props.changeCustomerMnpDataFormField({name: 'isHeadquartersAddressSame',value: event.target.value, entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
        }
    }

    onValueChange(target) {
         this.props.changeCustomerMnpDataFormField({...target, entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
    }

    recalculateConsent(target){
        this.props.changeCustomerMnpDataFormField({...target, entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData});
        this.props.requestRecalculateConsentsStrategy(this.props.entry.bundleNo, target.value);
    }

    getMigrationDateRange(bound) {
        if (this.props.mnpFormData && this.props.mnpFormData.migrationDateRanges) {
            return this.props.mnpFormData.migrationDateRanges.filter(el => el.migrationType === this.props.entry.migrationMode).reduce(el => el)[bound];
        }
    }

    getPropsForInput(name, isSelectBox) {
        let props = {
            name,
            id: this.wrapWithBundleNo(name),
            defaultValue: (isSelectBox ? this.props.entry[name] : "") || "",
            selected: this.props.entry[name],
            errors: this.props.entry.errors && this.props.entry.errors[name]
        };
        if(isSelectBox) {
            props.onChange = this.onValueChange.bind(this);
        } else {
            props.onBlur = this.onValueChange.bind(this);
        }
        return props;
    }

    getOptionsFromState(attribute, sortByPriority) {
        if (this.props.mnpFormData && this.props.mnpFormData[attribute] && this.props.mnpFormData[attribute] instanceof Array) {
            let finalElement = this.props.mnpFormData[attribute].slice();

            if (sortByPriority) {
                finalElement.sort((a, b) => a.priority - b.priority)
            }

            return finalElement;
        }
        return [];
    }

    getContactMethods() {
        return this.props.getMnpContactMethods(this.props.mnpFormData.contactMethods)
    }


    transformMsisdn(number) {
        return number.replace(/^(\d{3})(\d{3})(\d{3})$/, function () {
            return arguments[1] + '\xa0' + arguments[2] + '\xa0' + arguments[3];
        })
    }

    getMnpFormDataByAttribute(attribute){
        if(attribute === 'migrationModes') {
            return this.getMigrationModes();
        } else {
            return this.props.mnpFormData[attribute];
        }
    }

    getTooltipContentForProperty(attribute, entryProperty) {
        if (this.props.mnpFormData && this.props.mnpFormData[attribute] && this.props.mnpFormData[attribute] instanceof Array && this.props.entry[entryProperty]) {
            return this.getMnpFormDataByAttribute(attribute).filter(el => el.value === this.props.entry[entryProperty]).reduce(el => el).tooltipDescription;
        }
    }

    sortOperators(operators) {
        let sortedOperators = operators.slice().sort((a, b) => {
            if(a.priority === b.priority)
                return a.description.localeCompare(b.description);
            else
                return a.priority - b.priority;
        });

        if (this.props.mnpData && this.props.mnpData[this.props.entryIndex] && this.props.mnpData[this.props.entryIndex].operator) {
            let firstElement = sortedOperators.filter(el => el.value === this.props.mnpData[this.props.entryIndex].operator || this.props.mnpData[this.props.entryIndex].operator === 'NON CONFIGURED').reduce(el => el);
            sortedOperators.splice(sortedOperators.indexOf(firstElement), 1);
            sortedOperators.unshift(firstElement);
        }

        return sortedOperators;
    }

    getMigrationModes() {
        return this.getOptionsFromState('migrationModes', true).filter(el => el.operatorOfferTypeCode === this.props.entry.offerType);
    }

    componentDidMount() {
        this.props.registerNextStepCondition('mnpData');

        if(this.props.entry['operator'] === null) {
            let selectedOperator = this.sortOperators(this.getOptionsFromState('operators', true))[0].value;
            this.props.changeCustomerMnpDataFormField({
                name: 'operator',
                value: selectedOperator,
                entryIndex: this.props.entryIndex,
                defaults: this.props.mnpFormData
            });
        }

        if (this.props.isB2B && this.props.legalForm==='JDG') {
            this.props.changeCustomerMnpDataFormField({name: 'agreementType',value: '2', entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
        }
    }

    getPropsForInputWithEmptyOption(name, isEmptyOption) {
        return {
            name,
            id: this.wrapWithBundleNo(name),
            selected: isEmptyOption ? undefined : this.props.entry[name],
            errors: this.props.entry.errors && this.props.entry.errors[name],
            onChange: this.onValueChange.bind(this),
            withEmptyOption: isEmptyOption
        };
    }

    contactMethodSection() {
        return(
            <div>
                <div className="l-row">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <h4>{this.getDescription("contactMethodTitle")}</h4>
                    </div>
                </div>
                <div className="l-row u-spacing-l-xl u-no-spacing-top">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <ErrorRow errors={this.props.entry.errors && this.props.entry.errors.contactMethod}>
                            <OraSelect {...this.getPropsForInputWithEmptyOption('contactMethod', false)}
                                       label={this.getDescription("contactMethodTitle")}
                                       options={this.getContactMethods()} />
                        </ErrorRow>
                    </div>
                    <div className="l-col l-col-6 l-col-small-12">
                        <p>{this.getTooltipContentForProperty('contactMethods', 'contactMethod')}</p>
                    </div>
                </div>
            </div>
        )
    }

    handleSameMnpData(event) {
        this.props.switchSameMnpData()();
    }

    sameDataCheckboxSection() {
        return(
            <div className="l-row u-spacing-top-l-xl">
                <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                    <label className="o-checkbox opl-checkbox">
                        <input type="checkbox" onChange={event => {this.handleSameMnpData(event)}}/>
                        <span className="o-ci"/>
                        <span className="o-ci-label">{"Dla pozostałych przenoszonych numerów tak samo jak powyżej"}</span>
                    </label>
                </div>
            </div>
        )
    }

    migratedMSISDNSection() {
        return(
            <div className="l-row u-small-no-margin">
                <Toggle>
                    <h3>
                        <div className="u-spacing-s">
                            {this.getDescription("mnpNumberText")}
                            {this.props.mnpData && this.props.mnpData[this.props.entryIndex] && " " + this.transformMsisdn(this.props.mnpData[this.props.entryIndex].mobileNumber)}
                        </div>
                    </h3>
                </Toggle>
            </div>
        )
    }
    migrationModeSection(selectedMigrationMode) {
        return (
                <div>
                    <div className="l-row">
                        <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                            <h4>{this.getDescription("transferModeTitle")}</h4>
                        </div>
                    </div>
                    <div className="l-row u-spacing-l-xl u-no-spacing-top">
                        <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                            <ErrorRow errors={this.props.entry.errors && this.props.entry.errors.migrationMode}>
                                <OraSelect label={this.getDescription("transferModeTitle")}
                                           {...this.getPropsForInput('migrationMode', true)}
                                           options={this.getMigrationModes()}
                                />
                            </ErrorRow>
                        </div>
                        <div className="l-col l-col-6 l-col-medium-6 l-col-small-12">
                            <p>{this.getTooltipContentForProperty('migrationModes', 'migrationMode')}</p>
                        </div>
                    </div>
                    { selectedMigrationMode.showDateInput &&
                        <div className="l-row u-spacing-l-xl u-no-spacing-top">
                            <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                                <ErrorRow errors={this.props.entry.errors && this.props.entry.errors.date}>
                                    <DatePicker key={'migrationMode'+"_datePicker"}
                                        id={this.wrapWithBundleNo("date")}
                                        name="date"
                                        onChange={this.onValueChange.bind(this)}
                                        value={this.props.entry.date}
                                        options={{
                                            disabledWeekDays: [],
                                            minDate: this.getMigrationDateRange(DateRangeBoundaries.MIN),
                                            maxDate: this.getMigrationDateRange(DateRangeBoundaries.MAX)
                                        }}
                                        floatingLabel={selectedMigrationMode.dateLabel}
                                    />
                                </ErrorRow>
                            </div>
                        </div>
                    }
                </div>
            )
    }

    offerTypeSection() {
        return <div>
            <div className="l-row">
                <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                    <h4>{this.getDescription("offerTypeTitle")}</h4>
                </div>
            </div>
            <div className="l-row u-spacing-l-xl u-no-spacing-top">
                <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                    <div className="u-table u-w-100">
                        <div className="u-table-cell u-w-100 u-no-padding-right">
                            <ErrorRow errors={this.props.entry.errors && this.props.entry.errors.offerType}>
                                <OraSelect {...this.getPropsForInput('offerType', true)}
                                           label={this.getDescription("offerTypeTitle")}
                                           options={this.getOptionsFromState('offerTypes')}
                                           withEmptyOption={true}
                                           emptyOption={{
                                               value: '',
                                               description: this.getDescription("offerTypeDefaultText")
                                           }}
                                           onChange={this.recalculateConsent.bind(this)}/>
                            </ErrorRow>
                        </div>
                        <MulticartMnpTooltip
                            content={this.props.descriptions.offerTypeTooltip || this.props.offerTypeTooltip}
                            className="u-table-cell"/>
                    </div>
                </div>
            </div>
        </div>;
    }

    currentOperatorSection() {
        return <div>
            <div className="l-row">
                <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                    <h4>{this.getDescription("currentOperatorText")}</h4>
                </div>
            </div>
            <div className="l-row u-spacing-l-xl u-no-spacing-top">
                <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                    <div className="u-table u-w-100">
                        <div className="u-table-cell u-w-100 u-no-padding-right">
                            <ErrorRow errors={this.props.entry.errors && this.props.entry.errors.operator}>
                                <OraSelect {...this.getPropsForInput('operator', true)}
                                           label={this.getDescription("currentOperatorText")}
                                           options={this.sortOperators(this.getOptionsFromState('operators', true))}/>
                            </ErrorRow>
                        </div>
                        <MulticartMnpTooltip
                            content={this.props.descriptions.operatorTooltip || this.props.operatorTooltip}
                            className="u-table-cell"/>
                    </div>
                </div>
            </div>
        </div>;
    }

    temporalNumberSection(temporalNumberExists) {
        return (<div className="l-row u-spacing-l">
            <div className="l-col l-col-3 l-col-small-12">
                <div className="u-spacing-s">{this.getDescription("temporalNumberText")}</div>
                {temporalNumberExists &&
                <div className="l-col u-no-padding-left ">
                    <div className="u-table-cell u-w-50 u-no-padding-right">
                        <h4>{this.transformMsisdn(this.props.mnpData[this.props.entryIndex].temporalNumber)}</h4>
                    </div>
                    <MulticartMnpTooltip
                        content={this.props.descriptions.temporalNumberTooltip || this.props.temporalNumberTooltip}
                        className="u-table-cell u-vertical-top u-small-hide"
                        tooltipClassName="o-tooltip__trigger o-tooltip--top g-icon g-icon--only g-icon--hint g-icon--xs-s u-spacing-left-s"/>
                    <MulticartMnpTooltip
                        content={this.props.descriptions.temporalNumberTooltip || this.props.temporalNumberTooltip}
                        className="u-table-cell u-vertical-top u-medium-hide u-large-hide"
                        tooltipClassName="o-tooltip__trigger o-tooltip--top g-icon g-icon--only g-icon--hint g-icon--xs u-spacing-left-s"/>
                </div>
                }
                {!temporalNumberExists &&
                <div className="l-col u-no-padding-left ">
                    <div className="u-table-cell u-w-50 u-no-padding-right">
                        <h4>{this.getDescription("missingTemporalNumberText")}</h4>
                    </div>
                </div>
                }
            </div>
        </div>);
    }

    agreementSection() {
        return <div>
            {this.props.entry.agreementType !== '3' &&
            [
                <div className="l-row">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <h4>{this.getDescription("currentContractTitle")}</h4>
                    </div>
                </div>,
                <div className="l-row u-spacing-l-xl u-no-spacing-top">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <fieldset key={this.wrapWithBundleNo("fieldset")}
                                  className="opl-radiogroup opl-inputgroup opl-inputgroup--vertical">
                            <legend className="u-acc-hide">{this.getDescription("currentContractText")}</legend>
                            <OraSimpleRadio name={this.wrapWithBundleNo("agreementType")}
                                            checked={this.props.entry.agreementType === '1'}
                                            value="1"
                                            onChange={this.onAgreementTypeChange.bind(this)}
                                            key={this.wrapWithBundleNo("agreementType-1")}
                                            text={this.getDescription("naturalPersonOption")}/>
                            <OraSimpleRadio name={this.wrapWithBundleNo("agreementType")}
                                            checked={this.props.entry.agreementType === '2'}
                                            value="2"
                                            onChange={this.onAgreementTypeChange.bind(this)}
                                            key={this.wrapWithBundleNo("agreementType-2")}
                                            text={this.getDescription("economicActivityOption")}/>
                        </fieldset>
                    </div>
                </div>
            ]
            }

            {this.props.entry.agreementType === '2' && !this.props.isB2B &&
            <div>
                <div className="l-row u-spacing u-spacing-l-xl u-no-spacing-top">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <h4>{this.getDescription("economicActivityTitle")}</h4>
                    </div>
                </div>
                <div className="l-row u-spacing-l-xl u-no-spacing-top">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12 u-spacing">
                        <LabeledInput
                            label={this.getDescription("economicActivityText")} {...this.getPropsForInput('businessName', true)}
                            labelType="floating"/>
                    </div>
                </div>
                <div className="l-row u-spacing-l-xl u-no-spacing-top">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12 u-spacing">
                        <OraSelect {...this.getPropsForInput('identifier', true)}
                                   options={[{value: 'NIP', description: 'NIP'}, {
                                       value: 'REGON',
                                       description: 'REGON'
                                   }]}/>
                    </div>
                </div>
                {this.props.entry.identifier === 'NIP' &&
                <div className="l-row u-spacing-l-xl u-no-spacing-top">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <LabeledInput label={this.getDescription("nipText")} {...this.getPropsForInput('nip', true)}
                                      labelType="floating"/>
                    </div>
                </div>
                }
                {this.props.entry.identifier === 'REGON' &&
                <div className="l-row u-spacing-l-xl u-no-spacing-top">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <LabeledInput label={this.getDescription("regonText")} {...this.getPropsForInput('regon', true)}
                                      labelType="floating"/>
                    </div>
                </div>
                }
                <div className="l-row">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <h4>{this.getDescription("headquartersAddressTitle")}</h4>
                    </div>
                </div>
                <div className="l-row">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <fieldset className="opl-radiogroup opl-inputgroup opl-inputgroup--vertical">
                            <label className="o-radio opl-radio u-padding-right-l">
                                <OraSimpleRadio type="radio"
                                                checked={this.props.entry.isHeadquartersAddressSame === true}
                                                name="isHeadquartersAddressSame"
                                                value={'true'}
                                                onChange={this.onHeadquartersAddressSameChange.bind(this)}
                                                id={this.wrapWithBundleNo("isHeadquartersAddressSame-1")}/>
                                <span className="o-ci"/>
                                <span className="o-ci-label">{this.getDescription("sameAddressOption")}</span>
                            </label>
                            <label className="o-radio opl-radio">
                                <OraSimpleRadio type="radio"
                                                checked={this.props.entry.isHeadquartersAddressSame === false}
                                                name="isHeadquartersAddressSame"
                                                value={'false'}
                                                onChange={this.onHeadquartersAddressSameChange.bind(this)}
                                                id={this.wrapWithBundleNo("isHeadquartersAddressSame-2")}
                                />
                                <span className="o-ci"/>
                                <span className="o-ci-label">{this.getDescription("otherAddressOption")}</span>
                            </label>
                            {this.props.entry.errors.isHeadquartersAddressSame}
                        </fieldset>
                    </div>
                </div>
                <div className="l-row">
                    {this.props.entry.isHeadquartersAddressSame === false && !this.props.isB2B &&
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <MulticartMnpBusinessAddressComponent {...this.props} onChange={this.onValueChange.bind(this)}/>
                    </div>
                    }
                </div>
            </div>
            }
            {this.props.entry.agreementType === '1' && this.props.isB2B &&
            <div>
                <div>
                    <div className="l-row u-spacing u-spacing-l-xl u-no-spacing-top">
                        <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                            <h4>{"Osoba fizyczna"}</h4>
                        </div>
                    </div>

                    <div className="l-row u-spacing-l u-no-spacing-top">
                        <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                            <LabeledInput {...this.getPropsForInput('firstName', true)} label="Imię"
                                          labelType="floating"/>
                        </div>
                    </div>
                    <div className="l-row u-spacing-l u-no-spacing-top">
                        <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                            <LabeledInput {...this.getPropsForInput('lastName', true)} label="Nazwisko"
                                          labelType="floating" className="u-spacing-top-l"/>
                        </div>
                    </div>
                    <div className="l-row u-spacing-l u-no-spacing-top">
                        <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                            <LabeledInput {...this.getPropsForInput('idNumber', true)}
                                          label="Dowód osobisty"
                                          mask="aaa999999"
                                          placeholder="______"
                                          labelType="floating"
                                          className="u-spacing-top-l"/>
                        </div>
                    </div>
                    <div className="l-row u-spacing-l u-no-spacing-top">
                        <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                            <LabeledInput
                                {...this.getPropsForInput('pesel', true)}
                                label='Pesel'
                                mask="99999999999"
                                placeholder="___________"
                                labelType="floating"
                                className="u-spacing-top-l"/>
                        </div>
                    </div>
                </div>
                <div className="l-row">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <h4>{this.getDescription("headquartersAddressTitle")}</h4>
                    </div>
                </div>
                <div className="l-row">
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <fieldset className="opl-radiogroup opl-inputgroup opl-inputgroup--vertical">
                            <label className="o-radio opl-radio u-padding-right-l">
                                <OraSimpleRadio type="radio"
                                                checked={this.props.entry.isHeadquartersAddressSame === true}
                                                name="isHeadquartersAddressSame"
                                                value={'true'}
                                                onChange={this.onHeadquartersAddressSameChange.bind(this)}
                                                id={this.wrapWithBundleNo("isHeadquartersAddressSame-1")}/>
                                <span className="o-ci"/>
                                <span className="o-ci-label">{this.getDescription("sameAddressOption")}</span>
                            </label>
                            <label className="o-radio opl-radio">
                                <OraSimpleRadio type="radio"
                                                checked={this.props.entry.isHeadquartersAddressSame === false}
                                                name="isHeadquartersAddressSame"
                                                value={'false'}
                                                onChange={this.onHeadquartersAddressSameChange.bind(this)}
                                                id={this.wrapWithBundleNo("isHeadquartersAddressSame-2")}
                                />
                                <span className="o-ci"/>
                                <span className="o-ci-label">{this.getDescription("otherAddressOption")}</span>
                            </label>
                            {this.props.entry.errors.isHeadquartersAddressSame}
                        </fieldset>
                    </div>
                </div>
                <div className="l-row">
                    {this.props.entry.isHeadquartersAddressSame === false && this.props.isB2B &&
                    <div className="l-col l-col-4 l-col-medium-6 l-col-small-12">
                        <MulticartMnpBusinessAddressComponent {...this.props} onChange={this.onValueChange.bind(this)}/>
                    </div>
                    }
                </div>
            </div>
            }
        </div>;
    }

    render() {
        let selectedMigrationMode = this.getMigrationModes().find(mm => mm.value === this.props.entry.migrationMode);
        let temporalNumberExists = this.props.mnpData && this.props.mnpData[this.props.entryIndex] && this.props.mnpData[this.props.entryIndex].temporalNumber;
        let notOnlySecondStep  = this.props.mnpData.filter(e => !OnlineUtils.isMnpApplicationSecondStep(e.processType)).length>0;
        let isSecondStep = OnlineUtils.isMnpApplicationSecondStep(this.props.entry['processType']);
        return (

            <div className="opl-form">
                {this.props.entryIndex === 0 && notOnlySecondStep && this.contactMethodSection()}
                <Expander scrollToSelected={false} variant="section" sectionClassName="is-expanded">
                    <Section header={this.migratedMSISDNSection()}
                             headerBelow={false}>
                        {this.temporalNumberSection(temporalNumberExists)}
                        {!isSecondStep && this.currentOperatorSection()}
                        {!isSecondStep && this.offerTypeSection()}
                        {!isSecondStep && this.agreementSection()}
                        {!isSecondStep && this.getMigrationModes().length > 0 && this.migrationModeSection(selectedMigrationMode)}
                        {!isSecondStep && this.props.entryIndex === 0 && this.props.mnpData && this.props.mnpData.length > 1 && this.props.isMnpDataFilled && this.props.isB2B && this.sameDataCheckboxSection()}
                    </Section>
                </Expander>
            </div>
        )
    }


    getDescription(descName) {
        return this.props.descriptions[descName] || this.props[descName];
    }
}

